<template>
  <div>
    <span class="big_box">
      <!-- 房间号-入住时段 -->
      <section id="roomTime">
        <div class="roomBox">
          <div class="w20">房间号</div>
          <div class="select w70">
            <div class="c00">
              {{now_info.roomSn}}&ensp;
            </div>
          </div>
        </div>
        <div
          class="roomBox"
          @click="reset"
        >
          <div class="w20">入住时段</div>
          <div class="select w70">
            <div class="c00">现在入住</div>&ensp;至&ensp;
            <div class="c00">
              {{ now_info.end_time }}&ensp;共{{ now_info.diffDay }}晚
              <br />
            </div>
            <img
              src="@/assets/right.png"
              alt
            />
          </div>
        </div>
      </section>
      <!-- 入住人信息 -->
      <section id="roomTime">
        <div class="roomBox">
          <div class="leftBox c00 w100">
            <div class="leftBox_1">
              <p class="w20">入住人信息</p>
              <div
                class="img_box w70"
                style="-border:1px solid red;"
              >
                <img
                  @click="lined"
                  v-if="isline==false"
                  src="@/assets/opened.png"
                  alt
                />
                <img
                  @click="lined"
                  v-else
                  src="@/assets/closed.png"
                  alt
                />
              </div>
            </div>
            <div class="f12 w90">填写入住人信息，顾客要完成人脸对比</div>
          </div>
        </div>
        <div v-if="isline==false">
          <div
            v-for="(item,i) in info_box"
            :key="i"
            class="infoBox"
          >
            <div class="roomBox">
              <div class="leftBox w100">
                <div>
                  <p class="w20">
                    {{item.name_l}}
                    <img
                      class="write"
                      src="@/assets/two/write.png"
                      alt
                    />
                  </p>
                  <input
                    :class="i!=0?'ww1':'ww2'"
                    type="text"
                    placeholder="请输入入住人姓名"
                    v-model="item.v_name"
                  />
                </div>
                <div>
                  <p class="w20">{{item.ID_l}}</p>
                  <input
                    class="f10"
                    :class="i!=0?'ww1':'ww2'"
                    type="text"
                    oninput="if(value.length>18)value=value.slice(0,18),alert('请检查身份证是否无误')"
                    placeholder="请输入身份证号码"
                    v-model="item.v_ID"
                  />
                </div>
              </div>
              <div
                class="img_box"
                @click="dealLiver(i)"
                v-if="i != 0"
              >
                <img
                  src="@/assets/two/dalete.png"
                  alt
                  class="bottomImg"
                />
              </div>
            </div>
          </div>
        </div>
        <div
          v-if="isline==false"
          @click="addLiver"
          class="addLiver"
        >+&ensp;添加入住人</div>
      </section>
      <!-- 手机号-备注信息 -->
      <section
        id="roomTime"
        class="mb60"
      >
        <div class="roomBox">
          <div class="w20">手机号</div>
          <input
            type="tel"
            class="w70"
            maxlength="11"
            placeholder="输入手机号"
            v-model="dataForm.phone"
          />
        </div>
        <div class="roomBox">
          <div class="w20">备注信息</div>
          <input
            type="text"
            class="w70"
            placeholder="选填"
            v-model="dataForm.description"
          />
        </div>
      </section>
      <!-- 按钮 -->
      <div
        class="set"
        @click="sure_live"
      >确定入住</div>
    </span>
  </div>
</template>
<script>
import { Toast } from 'mint-ui';
import { PostBusinessOrderHandleRoomByCooper } from '@/api/api_business';
export default {
    data() {
        return {
            now_info: '',
            o_deposit: '',
            deposit: false,
            remark: '',
            orderSourceMap: [],
            PorList: [],
            payMent: [],
            show_payment: false,
            splitNum: '',
            c_hotel: '',
            isline: false,
            info_box: [
                {
                    name_l: '真实姓名',
                    v_name: '',
                    ID_l: '身份证号',
                    v_ID: ''
                }
            ],
            aaa: [],
            opened: false,
            disCount: [],
            count_deposit: false,
            PorList: [],
            scaleName: '',
            show_scaleName: false,
            phone: '',
            bcakPrice: false,
            radio: '0',
            dataForm: {
                orderType: 1,
                roomUser: [],
                phone: null,
                deposit: null,
                realAmount: null,
                checkOutFree: null,
                elecPayModel: null
            },
            air_statu: false
        };
    },
    created() {
        this.$store.commit('setHeader', '协议单位创建订单');
        this.now_info = this.$route.query;
    },
    methods: {
        // 入住人显示或影藏
        lined() {
            this.isline = !this.isline;
        },
        // 重选入住时段
        reset() {
            setTimeout('window.history.go(-1)', 200);
        },
        // 添加入住人
        addLiver() {
            var first_add = {
                name_l: '真实姓名',
                v_name: '',
                ID_l: '身份证号',
                v_ID: ''
            };
            this.info_box.push(first_add);
        },
        // 删除入住人
        dealLiver(i) {
            if (i != 0) {
                this.info_box.splice(i, 1);
            }
        },
        // 确定入住
        sure_live() {
            let arr = [];
            if (this.isline == false) {
                for (var i = 0; i < this.info_box.length; i++) {
                    if (!this.info_box[i].v_name) {
                        Toast({
                            message: '请正确填写姓名',
                            duration: 900
                        });
                        return false;
                    }
                    let obj = {};
                    obj.roomUser = this.info_box[i].v_name;
                    obj.idCard = this.info_box[i].v_ID;
                    arr.push(obj);
                }
                this.dataForm.roomUser = JSON.stringify(arr);
            }
            this.set_ok();
        },
        // 协议单位手动分房-确定
        set_ok() {
            let obj = { ...this.dataForm, ...this.now_info };
            PostBusinessOrderHandleRoomByCooper(obj).done((res) => {
                Toast({
                    message: res.msg,
                    duration: 2000
                });
                setTimeout('window.history.go(-1)', 2000);
            });
        }
    }
};
</script>
<style scoped>
.c_88 {
    color: #888;
    font-size: 0.7rem;
    font-weight: normal;
}
.display {
    align-items: center;
}
img {
    width: 30px;
    height: 18px;
}

.ww1 {
    width: 67.2%;
    box-sizing: border-box;
}
.ww2 {
    width: 70%;
}
.w20 {
    width: 26%;
    display: flex;
    align-items: baseline;
}
.w70 {
    width: 70%;
}
.f1 {
    font-size: 0.8rem;
}
.write {
    width: 8px;
    height: 8px;
    padding-left: 4px;
}
.cb2 {
    color: #b2b2b2;
    font-weight: normal;
}
.flex {
    display: flex;
    justify-content: space-between;
}

.w46 {
    width: 46%;
}
.w24 {
    width: 24%;
}
.w40 {
    width: 40%;
}
.w54 {
    width: 54%;
}
.c00 {
    color: #000;
    font-weight: bold;
}
.mr6 {
    margin-right: 6px;
    width: 20%;
    text-align: right;
}
.f12 {
    font-size: 0.6rem;
    color: #b2b2b2;
    font-weight: normal;
}
.f10 {
    font-size: 1rem;
}
.f14 {
    font-size: 0.7rem;
}
.f9 {
    font-size: 0.9rem;
    color: #000;
}
.w71 {
    width: 71%;
}
.w72 {
    width: 72%;
}
.w100 {
    width: 100%;
}
.mb60 {
    margin-bottom: 60px;
}
.big_box {
    position: fixed;
    top: 50px;
    bottom: 0;
    left: 0;
    right: 0;
    overflow-y: scroll;
}
#roomTime {
    margin-top: 20px;
    font-size: 0.8rem;
    color: #888;
}
#roomTime > .roomBox {
    background: #fff;
    line-height: 20px;
    display: flex;
    justify-content: space-between;
    padding: 10px 16px;
    box-sizing: border-box;
    width: 100%;
    border-bottom: 1px solid #f6f6f6;
    text-align: left;
}
#roomTime > .roomBox > .select {
    display: flex;
    justify-content: space-between;
}
#roomTime > .roomBox > .select > img {
    width: 20px;
    height: 20px;
}
#roomTime > .roomBox > .select > select {
    border: none;
    font-size: 0.7rem;
    background: #fff;
}
#roomTime > .roomBox > .input > .priceBox {
    white-space: nowrap;
    padding: 10px 0;
}
#roomTime > .roomBox > .input > div {
    padding: 10px 0;
}
#roomTime > .roomBox > .input > div > span {
    color: red;
}
#roomTime > .roomBox > .deposit {
    display: flex;
}
.van-radio-group--horizontal {
    flex-wrap: inherit;
}
/deep/ .van-radio__icon--checked .van-icon {
    background-color: #f60;
    border-color: #f60;
}
#roomTime > .roomBox > .deposit > .open_close {
    display: flex;
}
.roomBox > .img_box {
    line-height: 24px;
    display: flex;
    align-items: center;
    width: 0%;
    margin-right: 10%;
}
.roomBox > .leftBox > .leftBox_1 {
    padding-bottom: 4px;
    display: flex;
    justify-content: space-between;
}
.roomBox > .leftBox > .leftBox_1 > .img_box {
    text-align: right;
}

.scaleNameBox {
    width: 100%;
    font-size: 0.8rem;
    line-height: 30px;
}
.infoBox > .roomBox {
    display: flex;
    justify-content: space-between;
    padding: 10px 18px 0;
    margin: 4px 0;
    background: #fff;
    text-align: left;
}
.infoBox > .roomBox > .leftBox > div {
    display: flex;
    justify-content: space-between;
    line-height: 44px;
    border-bottom: 1px solid #f6f6f6;
}
input {
    font-size: 0.8rem;
    padding: 0;
    margin: 0;
    color: #000;
}
input::-webkit-input-placeholder {
    color: #aaa;
    font-size: 0.8rem;
}
input:-moz-placeholder {
    color: #aaa;
    font-size: 0.8rem;
}
input::-moz-placeholder {
    color: #aaa;
    font-size: 0.8rem;
}
input:-ms-input-placeholder {
    color: #aaa;
    font-size: 0.8rem;
}

#roomTime > .roomBox > .input > .f1::-webkit-input-placeholder {
    color: #aaa;
    line-height: 0.7rem;
    font-size: 0.8rem;
}
#roomTime > .roomBox > .input > .f1:-moz-placeholder {
    color: #aaa;
    line-height: 0.7rem;
    font-size: 0.8rem;
}
#roomTime > .roomBox > .input > .f1::-moz-placeholder {
    color: #aaa;
    line-height: 0.7rem;
    font-size: 0.8rem;
}
#roomTime > .roomBox > .input > .f1:-ms-input-placeholder {
    color: #aaa;
    line-height: 0.7rem;
    font-size: 0.8rem;
}
.set {
    position: fixed;
    bottom: 0;
    left: 0;
    height: 50px;
    line-height: 50px;
    color: #fff;
    background: rgba(255, 125, 65, 1);
    font-size: 0.8rem;
    width: 100%;
}
#roomTime > .addLiver {
    color: #2e7bf7;
}
#colorBox:active {
    border: 1px solid red solid rgba(255, 125, 65, 1);
    opacity: 0.95;
    cursor: pointer;
}
</style>



